<template>
  <div class="top">
    <div class="logo">
      <img src="../../assets/images/logo.png" alt="">
    </div>
    <div class="nav">
      <div>
        <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
          <el-menu-item index="1" @click="goOthers('/home')">情况概述</el-menu-item>
          <el-menu-item index="2" @click="goOthers('/joinUs')">加入我们</el-menu-item>
          <el-menu-item index="3" @click="goOthers('/group')">技术交流</el-menu-item>
          <el-menu-item index="4" @click="goOthers('/home')">资料整合</el-menu-item>
          <el-menu-item index="5" @click="goOthers('/home')">提问箱</el-menu-item>
        </el-menu>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        activeIndex: '1',
        activeIndex2: '1'
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      goOthers(url) {
        this.$router.push(url)
      }
    }
  }
</script>
<style scoped>
  .top {
    position: relative;
    display: grid;

  }




  .logo {
    height: 100%;
    position: absolute;
    left: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }

  .logo img {
    height: 48px;
    width: 161px;
  }

  ul {
    position: relative;
    z-index: 100;
    justify-content: end !important;
    -webkit-box-pack: end;
    display: -webkit-box;
  }

  ul li {
    margin-right: 5vw !important;
  }

  @media screen and (max-width: 800px) {

  }
</style>



